<template>
    <div class="login">
        <van-nav-bar left-arrow>
            <template #left>
                <div class="logo-box">
                    <div class="logo"></div>
                    <div class="logo-name">Luckin Coffee</div>
                </div>
            </template>
            <template #right>
                <div class="close" @click="close">跳过</div>
            </template>
        </van-nav-bar>
        <div class="welcome">
            <div class="welcome-cn">欢迎回来!</div>
            <div class="welcome-en">
                Please login to your
                <br />accounts
            </div>
        </div>
        <div class="field-box">
            <van-form>
                <van-field
                    v-model="userData.phone"
                    name="用户名"
                    label="用户名"
                    placeholder="用户名"
                    :rules="[{ required: true, message: '请填写用户名' }]"
                />
                <van-field
                    v-model="userData.password"
                    :type="isSeen ? 'text' : 'password'"
                    name="密码"
                    label="密码"
                    placeholder="密码"
                    :rules="[{ required: true, message: '请填写密码' }]"
                    :right-icon="isSeen ? 'eye-o' : 'closed-eye '"
                    @click-right-icon="seenPassword"
                />

                <!-- 忘记密码↓ -->
                <!-- <van-cell is-link @click="NoPassword" class="NoPassword">忘记密码？</van-cell>
                <van-popup
                    v-model="showNoPassword"
                    position="bottom"
                    round
                    closeable
                    close-icon="close"
                >
                    <div class="show-box">
                        <van-form>
                            <van-field
                                v-model="registerData.username"
                                label="手机号"
                                placeholder="手机号"
                            />
                            <van-field
                                v-model="sms"
                                center
                                clearable
                                label="短信验证码"
                                placeholder="请输入短信验证码"
                            >
                                <template #button>
                                    <van-button size="small" type="primary" color="#0b34ba">发送验证码</van-button>
                                </template>
                            </van-field>
                            <van-button
                                round
                                block
                                type="info"
                                native-type="submit"
                                class="submit"
                            >找回密码</van-button>
                        </van-form>
                    </div>
                </van-popup>-->
                <!-- 忘记密码↑ -->

                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit" @click="login">登 陆</van-button>
                </div>
            </van-form>
        </div>
        <div class="popup-box">
            <van-cell is-link @click="showPopup">注 册</van-cell>
            <van-popup v-model="show" position="bottom" round closeable close-icon="close">
                <div class="show-box">
                    <div class="register">注册</div>
                    <van-form>
                        <van-field v-model="registerData.phone" label="手机号" placeholder="手机号" />
                        <van-field v-model="registerData.nickName" label="昵称" placeholder="昵称" />
                        <van-field
                            v-model="registerData.password"
                            type="password"
                            label="密码"
                            placeholder="密码"
                        />
                        <div style="margin: 16px;">
                            <van-button
                                round
                                block
                                type="info"
                                native-type="submit"
                                @click="register"
                            >注 册</van-button>
                        </div>
                    </van-form>
                </div>
            </van-popup>
        </div>
    </div>
</template>

<script>
import "../assets/less/login.less";
import Vue from "vue";
import { Notify } from "vant";
Vue.use(Notify);
export default {
    data() {
        return {
            // 登陆时的数据
            userData: {
                phone: "",
                password: "",
            },
            // 注册时的数据
            registerData: {
                phone: "",
                nickName: "",
                password: "",
            },
            isSeen: false,
            show: false,
            showNoPassword: false,
            sms: "",
        };
    },
    methods: {
        // 序列化参数
        queryString(o) {
            let params = "";
            for (let key in o) {
                params += `${key}=${o[key]}&`;
            }

            return params.slice(0, -1);
        },
        // 查看密码
        seenPassword() {
            this.isSeen = !this.isSeen;
        },
        // 显示弹窗
        showPopup() {
            this.show = true;
        },
        // 验证数据
        valid(o) {
            for (let key in o) {
                if (!o[key].reg.test(o[key].value)) {
                    //提示
                    Notify({
                        type: "danger",
                        message: o[key].errorMsg,
                    });
                    //验证失败
                    return false;
                }
            }

            //验证通过
            return true;
        },
        //注册
        register() {
            let registerData = this.registerData;
            let data = {
                phone: {
                    value: registerData.phone,
                    reg: /^1([38]\d|4[5-9]|5[0-35-9]|6[56]|7[0-8]|9[189])\d{8}$/,
                    errorMsg: "请输入正确格式的手机号",
                },
                password: {
                    value: registerData.password,
                    reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                    errorMsg: "请输入正确格式的密码",
                },
                nickName: {
                    value: registerData.nickName,
                    reg: /1?/,
                    errorMsg: "",
                },
            };
            if (!this.valid(data)) {
                return;
            } else {
                // data.nickName = registerData.username;
                let info = Object.assign({}, registerData);
                info.appkey = this.appkey;
                let data = this.queryString(info);
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                    loadingType: "spinner",
                });
                this.axios({
                    method: "POST",
                    url: "/register",
                    data,
                })
                    .then((result) => {
                        this.$toast.clear();
                         
                        this.$toast("注册成功");
                        this.show = false
                    })
                    .catch((err) => {
                        this.$toast.clear();
                         
                    });
            }
        },
        // 登陆
        login() {
            let userData = this.userData;
            let data = {
                phone: {
                    value: userData.phone,
                    reg: /^1([38]\d|4[5-9]|5[0-35-9]|6[56]|7[0-8]|9[189])\d{8}$/,
                    errorMsg: "请输入正确格式的手机号",
                },
                password: {
                    value: userData.password,
                    reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                    errorMsg: "请输入正确格式的密码",
                },
            };
            if (!this.valid(data)) {
                return;
            } else {
                let info = Object.assign({}, userData);
                info.appkey = this.appkey;
                let data = this.queryString(info);
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                    loadingType: "spinner",
                });
                this.axios({
                    method: "POST",
                    url: "/login",
                    data,
                })
                    .then((result) => {
                        this.$toast.clear();
                         
                        if (result.data.code == 200) {
                            localStorage.setItem("CSTK", result.data.token);
                            this.$router.push({ name: "Home" });
                        } else {
                            this.$notify({
                                type: "warning",
                                message: result.data.msg,
                            });
                        }
                    })
                    .catch((err) => {
                        this.$toast.clear();
                         
                    });
            }
        },
        // 跳过登陆
        close(){
            this.$router.push({ name: "Home" });
        }
    },
};
</script>

<style lang="less" scoped>
</style>